* {
  margin: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans",
    "Helvetica Neue", sans-serif;
}
.App {
  background-color: var(--primary);
  background-image: none;
  background-size: cover;
  background-position: center center;
  width: 100%;
  min-height: 100vh;
  max-height: 100vh;
  display: flex;
  flex-direction: column;
  .Main {
    padding: 20px;
    img {
      width: 40%;
      height: 40%;
    }
    display: flex;
    flex: 1 1 0%;
    flex-direction: column;
    -webkit-box-pack: start;
    justify-content: flex-start;
    -webkit-box-align: center;
    align-items: center;
    width: 100%;
    background-image: url("./bg.png");
    background-size: cover;
    background-position: center center;
    .box {
      display: flex;
      align-items: center;
      justify-content: center;
      flex: 1 1 0%;
      flex-direction: column;
      width: 100%;
      .box2 {
        background-color: rgb(0, 0, 0);
        padding: 10px;
        border-radius: 24px;
        box-shadow: rgba(0, 0, 0, 0.7) 0px 5px 11px 2px;
        display: flex;
        flex: 2 1 0%;
        flex-direction: column;
        -webkit-box-pack: center;
        justify-content: center;
        -webkit-box-align: center;
        align-items: center;
        width: 95%;
        height: 60%;
        max-height: 570px;
        max-width: 530px;
        background-image: none;
        background-size: cover;
        background-position: center center;
        h1 {
          font-size: 45px;
          color: white;
          line-height: 2.1;
          font-weight: 600;
        }
        h2 {
          line-height: 2.4;
          font-size: 20px;
          color: white;
          font-weight: 400;
        }
        h3 {
          margin-top: 5px;
          text-align: center;
          color: white;
          line-height: 2;
          font-size: 14px;
          font-weight: 400;
        }
        .plusminus {
          display: flex;
          flex: 0 1 0%;
          flex-direction: row;
          -webkit-box-pack: center;
          justify-content: center;
          -webkit-box-align: center;
          align-items: center;
          width: 100%;
          background-image: none;
          background-size: cover;
          background-position: center center;
          margin-top: 60px;
          .buttonn {
            color: white;
            background-color: rgb(133, 103, 21);
            border-radius: 100%;
            border: none;
            padding: 10px;
            font-weight: bold;
            font-size: 15px;
            width: 30px;
            height: 30px;
            cursor: pointer;
            display: flex;
            -webkit-box-align: center;
            align-items: center;
            -webkit-box-pack: center;
            justify-content: center;
            box-shadow: rgba(250, 250, 250, 0.3) 0px 4px 0px -2px;
          }
          p {
            text-align: center;
            margin: 10px 20px;
            color: rgb(255, 255, 255);
            font-size: 16px;
            line-height: 1.6;
          }
        }
        .conn {
          margin: 20px;
          color: black;
          font-size: 100%;
          border-radius: 50px;
          border: none;
          background-color: rgb(3, 252, 236);
          padding: 10px;
          font-weight: bold;
          color: rgb(255, 255, 255);
          width: 100px;
          cursor: pointer;
          box-shadow: rgba(250, 250, 250, 0.3) 0px 6px 0px -2px;
        }
        .conn2 {
          margin: 20px;
          color: black;
          font-size: 150%;
          border-radius: 50px;
          border: none;
          background-color: rgb(3, 252, 236);
          padding: 10px;
          font-weight: bold;
          width: 100px;
          cursor: pointer;
          box-shadow: rgba(250, 250, 250, 0.3) 0px 6px 0px -2px;
        }
        p {
          font-size: 14px;
          line-height: 2.1;
          color: rgb(3, 252, 236);
          text-decoration: none;
        }
      }
    }
  }
}
